<template>
    <section class="create-memorial">
        <div class="create-memorial__container" v-if="this.memorialCreated">

            <header class="create-memorial__header">
                <router-link :to="{ name: 'memorials.create.step1' }" tag="button" class="default-arrow-btn create-memorial__btn-header">
                    <img class="arrow-right" src="~@/assets/img/icon/arrow-right.svg">
                    {{ $ml.get('create.memorial.common.buttons.back') }}
                </router-link>

                <div class="create-memorial__header-wrapper">
                    <router-link :to="{ name: 'home' }">
                        <img class="create-memorial__img" src="~@/assets/img/icon/welcome-screen-logo.svg" alt="Logo">
                    </router-link>
                </div>
            </header>

            <div class="create-memorial__form__wrapper_desciption">
                <h1 class="create-memorial__title third-header">
                    {{ $ml.with('name', this.memorialCreated.name).get('create.memorial.step2.title') }}
                </h1>

                <div class="create-memorial__decor"></div>

                <p class="create-memorial__title-text second-text">
                    {{ $ml.get('create.memorial.step2.description') }}
                </p>
            </div>
            <div class="create-memorial__form">
                <div class="create-memorial__form__wrapper">

                    <div class="create-memorial__form__wrapper__inputs height">
                        <form class="form">
                            <h5 class="form__title form__mb">
                                {{ $ml.get('create.memorial.step2.form.title') }}
                            </h5>

                            <div class="form__middle">
                                <div class="form__middle-descr">{{ $ml.get('create.memorial.step2.form.input.description') }}
                                </div>
                                <div class="form__middle-text" @input="saveText" contenteditable="true">
                                    {{ form.text }}
                                </div>
                                <a
                                        :href="form.link"
                                        class="form__middle-link"
                                        ref="link"
                                >{{ form.link }}</a>
                                <button type="button" class="form__middle-btn default-arrow-btn"
                                        @click.prevent="copyLink">
                                    <img src="~@/assets/img/icon/form-paper.svg" alt="">
                                    {{ $ml.get('create.memorial.step2.form.buttons.copy') }}
                                </button>
                            </div>

                            <div class="form__contact-icon">
                                <span>{{ $ml.get('create.memorial.step2.form.contacts.label') }}</span>
                                <a
                                        target="_blank"
                                        :href="fbShareUrl"
                                        class="form__contact-item"
                                >
                                    <img src="~@/assets/img/icon/facebook.svg" alt="">
                                </a>
                                <a
                                        target="_blank"
                                        class="form__contact-item"
                                        href="#"
                                        @click.prevent="shareToMessenger()"
                                >
                                    <img src="~@/assets/img/icon/facebook-messenger-1.svg" alt="">
                                </a>
                                <a
                                        target="_blank"
                                        :href="`whatsapp://send?text=${encodeURIComponent(shareText + '\n' + form.link)}`"
                                        class="form__contact-item"
                                        data-action="share/whatsapp/share"
                                >
                                    <img src="~@/assets/img/icon/whatsapp.svg" alt="">
                                </a>
                                <a
                                        v-if="user"
                                        target="_blank"
                                        :href="`mailto:?body=${encodeURIComponent(shareText + '\n' + form.link)}`"
                                        class="form__contact-item"
                                >
                                    <img src="~@/assets/img/icon/envelope-blue.svg" alt="">
                                </a>
                            </div>

                            <div class="form__notice"><img src="~@/assets/img/icon/form-star.svg" alt="">{{
                                $ml.get('create.memorial.step2.form.contacts.clarification') }}
                            </div>

                        </form>
                    </div>
                    <div class="form__btn-wrapper">
                        <button type="button" class="form__button-white default-second-btn"
                                @click.prevent="goTo('memorials.create.step3')"
                        >
                            <img class="arrow-left" src="~@/assets/img/icon/arrow-left-blue.svg">
                            {{ $ml.get('create.memorial.step2.form.buttons.payment') }}
                        </button>

                        <!--                        todo: change goTo('route name to show memorial')-->
                        <button type="button" class="form__button default-second-btn"
                                @click.prevent="goTo('memorials.edit', {id: memorialCreated.id})"
                        >
                            <img class="arrow-left" src="~@/assets/img/icon/arrow-left.svg">
                            {{ $ml.get('create.memorial.step2.form.buttons.start') }}
                        </button>
                    </div>
                </div>
            </div>
        </div>

        <sidebar :step="2" :memorial="memorialCreated"/>
        <HelpCenter/>
    </section>
</template>

<script>
    // @ is an alias to /src
    import {mapGetters, mapActions} from "vuex";
    import sidebar from '@/views/memorials/create/partials/sidebar';
    import HelpCenter from '@/components/HelpCenter.vue';

    export default {
        name: 'MemorialCreateStep2',
        components: {
            sidebar,
            HelpCenter
        },
        data() {
            return {
                form: {
                    text: '',
                    link: ''
                },
            }
        },
        computed: {
            ...mapGetters({
                memorial: 'memorial/getMemorialFormData',
                memorialCreated: 'memorial/getMemorialForCreating',
                step: 'memorial/getStep',
                shareText: 'memorial/getShareText',
                user: 'profile/getUser',
            }),
            fbShareUrl() {
                return `https://www.facebook.com/dialog/share?app_id=2062324473802315&display=popup&href=${encodeURIComponent(this.form.link)}&quote=${encodeURIComponent(this.shareText)}&redirect_uri=${encodeURIComponent(window.location.origin + '/memorials/create/step2')}`;
            }
        },
        async mounted() {
            await this.$store.dispatch('memorial/getMemorialForCreating');

            if (this.memorialCreated == null || this.memorialCreated.paid) {
                return this.goTo('memorials.create.step1');
            }

            this.$store.commit('memorial/setFormIsLoading', false);
            this.$store.commit('memorial/setStep', 2);

            //set share text
            let routeData = this.$router.resolve({name: 'memorials.story.auth', params: { slug: this.memorialCreated.slug }});
            this.form.link = `${window.location.origin}${routeData.href}`;

            if(!this.shareText) {
                this.form.text = this.$ml.with('name', this.memorialCreated.name).get('create.memorial.step2.form.input.text');
                await this.$store.dispatch('memorial/saveShareText', this.form.text);
            }
            else {
                if(this.shareText.indexOf(this.memorialCreated.name) === -1) {
                    this.form.text = this.$ml.with('name', this.memorialCreated.name).get('create.memorial.step2.form.input.text');
                    await this.$store.dispatch('memorial/saveShareText', this.form.text);
                }
                else {
                    this.form.text = this.shareText;
                }
            }
        },
        methods: {
            shareToMessenger() {
                let url = window.mobileCheck()
                    ? `fb-messenger://share?link=${encodeURIComponent(this.form.link)}`
                    : `https://www.facebook.com/dialog/send?app_id=2062324473802315&display=popup&link=${encodeURIComponent(this.form.link)}&msg=${encodeURIComponent(this.shareText)}&redirect_uri=${encodeURIComponent(window.location.origin + '/memorials/create/step2')}`

                window.open(url, '_blank');
            },
            saveText(event) {
                this.$store.dispatch('memorial/saveShareText', event.target.innerText);
            },
            copyLink() {
                let link = this.$refs['link'];

                if (null !== link && typeof link !== 'undefined') {
                    let href = link.getAttribute('href');

                    if (null !== href && typeof href !== 'undefined') {
                        navigator.clipboard.writeText(href);
                    }
                }
            },
            goTo(route, params = null) {
                this.$router.push({name: route, params});

                if(route == 'memorials.edit') {
                    this.$store.commit('memorial/setDefaultData');
                }
            }
        }
    }
</script>

<style lang="scss">
    @import "~@/assets/scss/pages/create-memorial.scss";
</style>